ом лото\" разыгрывают автомобили, загородные дома, путешествия и крупные денежные призы, но раньше победителям вручалась и бытовая техника:холодильники, телевизоры, утюги и пылесосы. ")]),_c('p',{staticClass:"app-about__text"},[_vm._v(" За последние 8 лет в программе разыграли больше 24 миллиардов рублей, состоялось несколько помолвок, выходили в эфир и участвовали в тиражах космонавты, а Михаил Борисов перетаскал такое количество мешков с бочонками, что, по его словам, мог бы вручную перегородить Енисей или построить ГЭС. ")]),_c('p',{staticClass:"app-about__text important"},[_vm._v(" Лотерея стала сразу всероссийской, она воспринималась как абсолютное чудо. В 90-е годы наша программа была одной из немногих, где действительно исполнялись мечты. ")]),_c('p',{staticClass:"last__child"},[_vm._v("Михаил Борисов в "),_c('a',{staticClass:"link",attrs:{"href":"#","target":"_blank"}},[_vm._v("интервью")]),_vm._v(" для «Дни.ру»")])])])}]

